.chat {
    flex: 0.65;
    display: flex;
    flex-direction: column;
    
}

.chat_header { 
    padding: 20px;
    display: flex;
    border-bottom: 1px solid lightgrey;
}

.chat_headerInfo {
    flex: 1;
    padding-left: 20px;
}

.chat_headerInfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat_headerInfo > p {
    color: grey;
    font-size: xx-small;
}

.chat_headerRight {
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat_message {
    position: relative ;
    padding: 10px;
    background-color: white;
    font-size: 16pxe;
    border-radius: 20px;
    width: fit-content;
    margin-bottom: 15px;
    margin-left: 15px;
    margin-top: 20px;
}

.chat_name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}

.chat_receiver {
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat_timeStamp {
    margin-left: 10px;
    font-size: xx-small;
}

.chat_body {
    flex: 1;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    /* background-color: blue; */
    overflow: scroll;
    /* display: none; */
}

.chat_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}

.chat_footer > form {
    flex: 1;
    display: flex;
}

.chat_footer > form > input {
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
}

.chat_footer > form > button { 
    display: none;
}

.chat_footer > .MuiSvgIcon-root { 
    padding: 10px;
    color: grey;
}